@mixin pad{
  @media(max-width: 992px){
    @content;
  }
}

@mixin mobile{
  @media(max-width: 767px){
    @content;
  }
}



@mixin text-hide{
  text-indent: 101%;
  overflow:hidden;
  white-space: nowrap;
}

@mixin text-color-size($color,$size){
  color:$color;
  font-size:$size;
}